import axios from '../axios'




  class  News {
   constructor() {
    this._category = 'technology'
    this._searchTerm = ''
    this._currentPage= 1
    this._totalPage=1
    this._pageSize = 12
   }
   
   
    async    getNews (){
       try{
     const {data} = await axios.get(this._url())
     this._totalPage = Math.ceil(data.totalResults/this._pageSize)
        return {
            articles: data.articles,
            totalResults:data.totalResults,
            currentPage:this._currentPage,
            category:this._category,
            totalPage:this._totalPage,
            isNext:this._isNext(),
            isPrev:this._isPrev()
        }
       
       
       }catch(e){
        throw new Error(e)
       }
    }

    next(){
      if(this._isNext()){
        this._currentPage++
        return this.getNews()
      }return false
    }
    prev(){
      if(this._isPrev()){
        this._currentPage--
        return this.getNews()
      }return false
    }

    setCurrentPage(page){
      if(page>=1 && page<=this._totalPage){
        this._currentPage = page
        return this.getNews()
      }return console.log('invaild page number')
      
    }

    changeCategory(Category){
      this._category= Category
      this._currentPage= 1
      return this.getNews()
    }

    handleSearch(term){
      this._searchTerm = term
      return this.getNews()
    }   
     _isNext(){
      return this._currentPage<this._totalPage
    }
    _isPrev(){
     return this._currentPage>1
    }

    _url =() =>{
       var url = '/?'
        if(this._category) url += `category=${this._category}`
        if(this._searchTerm) url +=`&q=${this._searchTerm}`
        if(this._currentPage) url += `&page=${this._currentPage}`
        if(this._pageSize) url +=    `&pageSize=${this._pageSize}`
       
        return url;
    }


}

 


export default News;