

import React from 'react'

function Card({ item }) {

    return (
        <div className='card me-3 mb-3'>
            <div className="img bg-dark">
                {item.urlToImage && <a href={item.url} target="_blank" rel="noopener noreferrer">
                <img src={item.urlToImage} alt={item.title} />
                </a>}
            </div>
            <div className="text mt-3 mx-3">
                <a href={item.url} target="_blank" rel="noopener noreferrer">
                    <h5 className='title'>{item.title}</h5>
                </a>
                <p className='deccription'>{item.description} </p>
            </div>
            <div className="d-flex mx-3">
                <p className='date'>pablished at: {item.publishedAt}</p>
                <button className="btn btn-light mx-3 ms-auto">{item.source.name} </button>
            </div>
        </div>
    )
}




export default function Main({news}) {
    return (
     
           <div className='d-flex flex-wrap my-1 justify-content-center'>
                
            {news && news.length === 0 && <h2 className='m-3'>there is no news</h2>}
            {news && news.map(item => <Card key={item.title} item={item} />)}
        </div>
       
    )
}
