import React, { Component } from 'react'
import Header from './components/header/header'
import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import Main from './components/main/index'
import News from './components/getNews'
import Loading from './components/Loading'
import Top from './components/Top'
import Pagination from './components/pagination/Pagination'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowUp } from '@fortawesome/free-solid-svg-icons'


const news = new News()

export default class App extends Component {


  constructor(props) {
    super(props)
  
    this.state = {
       data:{},
       isLoading:true
    }
  }
 componentDidMount(){
    news.getNews().then( data =>{
      this.setState({data,isLoading:false})
    })
  }


  Prev=()=>{
    this.setState({isLoading:true})
    news.prev().then(data =>{
      this.setState({data,isLoading:false})
    })
  
    }
  Next=()=>{
      this.setState({isLoading:true})
      news.next().then(data =>{
        this.setState({data,isLoading:false})
      })
    
   }
   
   setPage= (page) =>{
      this.setState({isLoading:true})

      news.setCurrentPage(page).then(data =>{
        this.setState({data,isLoading:false})
      })
   }
   handleCategory = (Category)=>{
      this.setState({isLoading:true})
      news.changeCategory(Category).then(data =>{
        this.setState({data, isLoading:false})
      })
   }

   searchTerm = (term)=>{
    this.setState({isLoading:true})
    news.handleSearch(term).then(data =>{
      this.setState({data,isLoading:false})
    })
   }

  render() {
    const {isLoading,data} = this.state
   const {articles,currentPage,totalPage,totalResults, isNext,isPrev,category} = data
    return (
      <div>
        <Header newsCategory={category} handleCategory={this.handleCategory} searchTerm={this.searchTerm} />
        { isLoading?(<Loading/>):(
          <>
            <Top totalResults={totalResults} totalPage={totalPage} currentPage={currentPage} />
            <Main news={articles}/>
            <Pagination Next={this.Next} page={this.setPage} Prev={this.Prev} isNext={isNext} isPrev={isPrev} currentPage={currentPage} totalPage={totalPage} />
            <button className="scrol-btn" onClick={()=>{
            window.scroll(0,0)
            }}><FontAwesomeIcon icon={faArrowUp} /></button>
          </>
        )}
       
      </div>
    )
  }
}
