import React, {  useState } from 'react'
import useWindoSize from '../../useWindowSize'
import { Category } from './Category'
import Input from './input'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faWindowClose } from '@fortawesome/free-solid-svg-icons'

export default function Header({ newsCategory,handleCategory,searchTerm }) {

    

    const [isSearch, setSearch] = useState(false)
    const { width } = useWindoSize()
    const ref = React.createRef()
    const search = () => {
        setSearch(!isSearch)
    }

    return (
        <div className='' >
            <div className="header">
                <h2 className='' ref={ref}>blockbuster headline</h2>
                {width > 830 && (
                    <>

                        <Input search={searchTerm} />
                    </>
                )}
                {width < 830 && (
                    isSearch ? (

                        <>
                            <FontAwesomeIcon icon={faWindowClose} size='lg' rotation={30} pull='right' onClick={search} />
                            <Input search={searchTerm} />
                        </>
                    ) : <FontAwesomeIcon icon={faSearch} onClick={search} size='lg' />

                )}

            </div>

            <div className="d-flex flex-wrap my-3 justify-content-center">
                {Category && Object.keys(Category).map(item => {
                    if (newsCategory === item) {
                        return (<button key={item} className="btn mx-1 btn-primary">{`#${item}`}</button>)
                    } else {
                        return (
                            <button key={item} value={item} onClick={e =>{handleCategory(e.target.value)}} className="btn mx-1 btn-light">{`#${item}`} </button>
                        )
                    }
                })}
            </div>
        </div>
    )
}
