import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'
import React, { useState } from 'react'


library.add(faSearch)

export default function Input({ search }) {

    const [searchTerm, setSearchTerm] = useState('')

    return (
        <div className='input-group'>
            <input className='form-control' type="search" onChange={(e) => { setSearchTerm(e.target.value) }} onKeyPress={(e) => { if (e.key === 'Enter') { search(searchTerm) } }} />
            <button className="btn btn-light" onClick={() => { search(searchTerm) }}><FontAwesomeIcon icon='search' size='lg' /> </button>
        </div>
    )
}
