import React, { useState } from 'react'

export default function Pagination({Prev,Next,isPrev,isNext,currentPage,totalPage,page}) {
    
    const [isEditable,setEditable] = useState(false)
    const [CurrentPage,setCurrentPage] = useState()

    const changePage = (key)=>{
        if(key === 'Enter'){
          page(CurrentPage)
        }
    }
    return (
        <div className='d-flex align-items-center my-3 mx-4'>
           <button className="btn btn-primary" disabled={!isPrev} onClick={()=>{Prev()}} >prev</button> 
            <div className='flex-grow-1 text-center'>
            {isEditable? (
                <input type='number' value={CurrentPage} onChange={(e)=>{setCurrentPage(e.target.value)}} onKeyPress={e=>{changePage(e.key)}}></input>
            ):(
                <div onDoubleClick={()=>setEditable(true)}>
                    page {currentPage} of {totalPage} <br />
                    <p className="small m-0">double tap to jump</p>
                </div>
            )}
            </div>
           <button className="btn btn-primary ms-auto" disabled={!isNext} onClick={()=>{Next()}}>next</button>
        </div>
    )
}
